@import url(https://fonts.googleapis.com/css?family=Heebo:400,700|Open+Sans:400,700);
:root {
  --hover-color: #3c3163;
  --transition-time: 1s;
}

.cards-wrapper {  
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;  
  margin: 0 auto;
}

.cards-wrapper a{
  text-decoration: none !important;
}

.card {
  --bg-filter-opacity: 0.5;  
  height: 20em;
  width: 15em;
  font-size: 1.5em;
  color: white;
  border-radius: 1em;
  padding: 1em;
  margin: 1em auto;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 5em -1em black;
  transition: all, 1s;
  transition: all, var(--transition-time);
  position: relative;
  overflow: hidden;
  border: 10px solid #ccc;
  text-decoration: none;
}

.card:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.card h1 {
  margin: 0;
  font-size: 1.5em;
  line-height: 1.2em;
}

.card p {
    text-align: left;
    font-size: 0.75em;
    font-family: 'Open Sans';
    margin-top: 0.5em;
    line-height: 2em;
}

.card:before, .card:after {
  content: '';
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  border-radius: 50%;
  position: absolute;
  left: -50%;
  top: -50%;
  z-index: -5;
  transition: all, 1s;
  transition: all, var(--transition-time);
  transition-timing-function: ease-in-out;
}

.card:before {
  background: rgba(221, 221, 221, 0.315);
  width: 250%;
  height: 250%;
}

.card:after {
  background: rgba(255, 255, 255, 0.418);
  width: 200%;
  height: 200%;
}

.card:hover {
  color: #3c3163;
  color: var(--hover-color);
}

.card:hover:before, .card:hover:after {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.info {
  font-size: 1.2em;
  display: flex;
  padding: 1em 3em;
  height: 3em;
}

.info img {
  height: 3em;
  margin-right: 0.5em;
}

.info h1 {
  font-size: 1em;
  font-weight: normal;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1285px) {
  .cards-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 900px) {
  .cards-wrapper {
    grid-template-columns: 1fr;
  }
  .info {
    justify-content: center;
  }  
}

@media screen and (max-width: 500px) {
  .cards-wrapper {
    padding: 4rem 2rem;
  }
  .card {
    max-width: calc(100vw - 4rem);
  }
}

@media screen and (max-width: 450px) {
  .info {
    display: block;
    text-align: center;
  }
  .info h1 {
    margin: 0;
  }
}
.dev-container {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    padding: 15px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
  }
  
  .social {
    width: 40px;
    height: 40px;
    background: #faebd78a;
    border-radius: 50%;  
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: -1;
    transition: 0.5s;
  }
  
  .name {
    color: #212529;
    font-size: 20px;  
    letter-spacing: -10px;
    position: absolute;
    z-index: -1;
    transition: 0.5s;
  }
  
  .dev-container img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
  }
  
  .dev-container:hover {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  
  .dev-container:hover .name {
    letter-spacing: normal;
    -webkit-transform: translateY(80px);
            transform: translateY(80px);
  }
  
  .dev-container:hover img {
    box-shadow: 0 0 20px rgb(129, 43, 128);
  }
  
  .dev-container:hover .social {
    transition-delay: 0.1s;
    box-shadow: 0 0 20px rgb(129, 43, 128);
  }
  
  .dev-container:hover .social:nth-child(1) {  
    -webkit-transform: rotate(-45deg) translateY(-90px);  
            transform: rotate(-45deg) translateY(-90px);
  }
  
  .dev-container:hover .social:nth-child(2) {
    -webkit-transform:  translateY(-90px);
            transform:  translateY(-90px);
  }
  
  .dev-container:hover .social:nth-child(3) {
    -webkit-transform:  rotate(45deg) translateY(-90px);
            transform:  rotate(45deg) translateY(-90px);
  }
  
  .dev-container a {
    transition: 1s;
    text-decoration: none;
    color: #212529;
  }
  
  .social:hover a {  
    color: rgb(129, 43, 128);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
